<template>
   <div class="filters">
      <span v-for="filter in filters" 
      :key="filter.value" 
      @click="$emit('change-filter',filter.value)"
      class="filter"
      :class="{active:selected===filter.value}">
      {{filter.label}}</span>
    </div>
</template>

<script>
export default {
    name:"TodoFilter",
    props:["selected"],
    setup(){
        const filters=[
          {label:"全部",value:"all"},
          {label:"未完成",value:"todo"},
            {label:"已完成",value:"done"},
             
            
        ];
        return{
            filters
        };
    }
}
</script>

<style>
/* 过滤选项 */
.filters{
  display:flex;
  margin:24px 2px;
  color:#c0c2ce;
  font-size:14px;
}
.filters .filter{
  margin-right: 14px;
  transition:0.8s;
  cursor: pointer;
}
.filters .filter.active{
  color:#6b729c;
  transform: scale(1.2);
}
</style>